@import url("https://fonts.googleapis.com/css2?family=Radio+Canada:wght@400;700&display=swap");
* {
  box-sizing: border-box;
}

html {
  height: 100vh;
  background: url("/img/background.gif") center no-repeat;
  background-size: cover;
  background-attachment: fixed;
  image-rendering: pixelated;
  font-family: "Radio Canada", sans-serif;
}

body {
  width: 90vw;
  min-height: 90vh;
  padding: 50px;
  margin: 50px 5vw;
  overflow-y: auto;
  background-color: rgba(109, 104, 117, 0.9);
  color: #FFCDB1;
  border-radius: 8px;
}
body h1 {
  margin-bottom: 8px;
}
body hr {
  border: 1px solid #FFB4A2;
  width: 200px;
  margin: 0px;
}

form {
  margin: 25px 0;
  padding: 24px;
  border-radius: 12px;
  background-color: #FFCDB1;
  color: #6D6875;
  font-weight: bold;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  gap: 25px;
}
form label {
  flex: 1;
  display: flex;
  flex-flow: column wrap;
}
form label:last-of-type {
  flex: 0 0 content;
}
form label input[type=text] {
  width: 100%;
  height: 24px;
  border: 1px solid #B5838D;
  border-radius: 4px;
  background: #FFB4A2;
}
form label input[type=text]:focus {
  outline: 2px solid #6D6875;
}
form label input[type=checkbox] {
  appearance: none;
  width: 24px;
  height: 24px;
  border: 1px solid #B5838D;
  border-radius: 4px;
  background: #FFB4A2;
  display: grid;
  place-content: center;
}
form label input[type=checkbox]:before {
  content: "";
  width: 16px;
  height: 16px;
  border-radius: 2px;
  transform: scale(0);
  box-shadow: inset 1rem 1rem #6D6875;
}
form label input[type=checkbox]:checked:before {
  transform: scale(1);
}
form button {
  padding: 12px;
  outline: 1px solid #B5838D;
  border: none;
  background: #6D6875;
  color: #FFCDB1;
  border-radius: 4px;
}
form button:hover {
  background: #FFB4A2;
  outline: 1px solid #B5838D;
  color: #6D6875;
}

table {
  width: 100%;
  margin: 25px auto;
  border-collapse: separate;
  border-spacing: 1px 4px;
}
table td,
table th {
  background-color: #FFCDB1;
  color: #6D6875;
  padding: 8px;
  border: 1px solid #FFCDB1;
}
table th {
  border-radius: 12px;
}
table td:first-child {
  text-align: center;
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
}
table td:last-child {
  text-align: center;
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;
}
table thead td {
  font-style: italic;
  font-weight: bold;
}
table tbody tr {
  height: 50px;
}
table .btn {
  background: none;
  border: none;
  font-size: 1.1rem;
}
table .btn:hover {
  cursor: pointer;
}